import axios from "axios";
const API_URL = "http://localhost:9921/timetable";

const TimetableService = {
  getTimetable: async () => {
    try {
        const response = await axios.get(`${API_URL}/`);
        console.log("response", response);
        return response.data;
      } catch (error) {
        console.error("Error fetching lecturers:", error);
        return [];
      }
  },

  generateTimetable: async () => {
    try {
      const response = await fetch(`${API_URL}/generate`, { method: "GET" });
      if (!response.ok) throw new Error("Failed to generate timetable");
    } catch (error) {
      console.error(error);
    }
  },

  downloadTimetable: async (activeDay, daysWithDates) => {
    const dayIndex = daysWithDates.findIndex(day => day.name === activeDay) + 1; // 1-based index
    const response = await fetch(`http://localhost:9921/timetable/pdf/days/${dayIndex}`);
    const blob = await response.blob();
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${activeDay}-Timetable.pdf`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    window.URL.revokeObjectURL(url);
  },
};

export default TimetableService;
