import axios from 'axios';

import API_BASE_URL from './HostConfig';

export const addClass = async (classData) => {
  try {
    const response = await axios.post(`${API_BASE_URL}/create`, classData);
    return response.data;
  } catch (error) {
    console.error("Error adding class:", error);
    throw new Error("Failed to add class.");
  }
};

export const fetchClasses = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/`);
    return response.data;
  } catch (error) {
    console.error("Error fetching classes:", error);
    throw new Error("Failed to fetch classes.");
  }
};

export const deleteClass = async (classId) => {
  try {
    await axios.delete(`${API_BASE_URL}/delete/${classId}`);
    return { success: true };
  } catch (error) {
    console.error("Error deleting class:", error);
    throw new Error("Failed to delete class.");
  }
};

export const updateClass = async (classId, classData) => {
  try {
    const response = await axios.put(`${API_BASE_URL}/update`, classData);
    return response.data;
  } catch (error) {
    console.error("Error updating class:", error);
    throw new Error("Failed to update class.");
  }
};

export const fetchClassById = async (classId) => {
  try {
    const response = await axios.get(`${API_BASE_URL}/${classId}`);
    return response.data;
  } catch (error) {
    console.error("Error fetching class:", error);
    throw new Error("Failed to fetch class details.");
  }
};