import React, { useState, useEffect } from 'react';
import { 
  fetchPrograms, 
  fetchPeriods, 
  fetchCourses, 
  fetchProgramCourses, 
  addCourseToProgram, 
  removeCourseFromProgram 
} from '../Services/programCourseService';
import {
  Container,
  Grid,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Box,
  CircularProgress,
} from '@mui/material';
import { Search, Delete, Add, Refresh } from '@mui/icons-material';
import TopContainer from './TopContainer';

const ProgramCourse = () => {
  // State variables
  const [programs, setPrograms] = useState([]);
  const [periods, setPeriods] = useState([]);
  const [courses, setCourses] = useState([]);
  const [programCourses, setProgramCourses] = useState([]);
  const [filteredProgramCourses, setFilteredProgramCourses] = useState([]);
  
  const [selectedProgram, setSelectedProgram] = useState('');
  const [selectedPeriod, setSelectedPeriod] = useState('');
  const [selectedCourse, setSelectedCourse] = useState('');
  
  const [programSearch, setProgramSearch] = useState('');
  const [courseSearch, setCourseSearch] = useState('');
  
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [courseToDelete, setCourseToDelete] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // Fetch initial data
  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        setLoading(true);
        const [programsData, periodsData, coursesData] = await Promise.all([
          fetchPrograms(),
          fetchPeriods(),
          fetchCourses()
        ]);
        setPrograms(programsData);
        setPeriods(periodsData);
        setCourses(coursesData);
      } catch (error) {
        setError('Failed to fetch initial data. Please try again.');
      } finally {
        setLoading(false);
      }
    };

    fetchInitialData();
  }, []);

  // Fetch program courses when program or period changes
  useEffect(() => {
    const getProgramCourses = async () => {
      if (selectedProgram && selectedPeriod) {
        try {
          setLoading(true);
          const data = await fetchProgramCourses(selectedProgram, selectedPeriod);
          setProgramCourses(data);
          setFilteredProgramCourses(data);
        } catch (error) {
          setError('Failed to fetch program courses. Please try again.');
        } finally {
          setLoading(false);
        }
      }
    };

    getProgramCourses();
  }, [selectedProgram, selectedPeriod]);

  // Filter program courses when courseSearch changes
  useEffect(() => {
    if (courseSearch) {
      const filtered = programCourses.filter(
        course => 
          course.courseCode?.toLowerCase().includes(courseSearch.toLowerCase()) ||
          course.courseName?.toLowerCase().includes(courseSearch.toLowerCase())
      );
      setFilteredProgramCourses(filtered);
    } else {
      setFilteredProgramCourses(programCourses);
    }
  }, [courseSearch, programCourses]);

  // Filter programs when programSearch changes
  const filteredPrograms = programs.filter(
    program => 
      program.programName?.toLowerCase().includes(programSearch.toLowerCase()) ||
      program.programCode?.toLowerCase().includes(programSearch.toLowerCase())
  );

  // Handlers
  const handleProgramChange = (event) => {
    setSelectedProgram(event.target.value);
    setCourseSearch('');
  };

  const handlePeriodChange = (event) => {
    setSelectedPeriod(event.target.value);
    setCourseSearch('');
  };

  const handleAddCourse = async () => {
    if (!selectedProgram || !selectedPeriod || !selectedCourse) {
      setError('Please select a program, period, and course.');
      return;
    }

    try {
      setLoading(true);
      await addCourseToProgram(selectedProgram, selectedPeriod, selectedCourse);
      
      // Refresh program courses after adding
      const updatedCourses = await fetchProgramCourses(selectedProgram, selectedPeriod);
      setProgramCourses(updatedCourses);
      setFilteredProgramCourses(updatedCourses);
      
      setSuccess('Course added successfully!');
      setIsAddDialogOpen(false);
      setSelectedCourse('');
      
      // Clear success message after 3 seconds
      setTimeout(() => setSuccess(''), 3000);
    } catch (error) {
      setError('Failed to add course. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const confirmDeleteCourse = (programCourseId) => {
    setCourseToDelete(programCourseId);
    setIsDeleteDialogOpen(true);
  };

  const handleDeleteCourse = async () => {
    if (!courseToDelete) return;

    try {
      setLoading(true);
      await removeCourseFromProgram(courseToDelete);

      // Refresh program courses after removing
      const updatedCourses = await fetchProgramCourses(selectedProgram, selectedPeriod);
      setProgramCourses(updatedCourses);
      setFilteredProgramCourses(updatedCourses);

      setSuccess('Course removed successfully!');
      setTimeout(() => setSuccess(''), 3000);
    } catch (error) {
      setError('Failed to remove course. Please try again.');
    } finally {
      setLoading(false);
      setIsDeleteDialogOpen(false);
      setCourseToDelete(null);
    }
  };

  const refreshData = async () => {
    if (selectedProgram && selectedPeriod) {
      try {
        setLoading(true);
        const data = await fetchProgramCourses(selectedProgram, selectedPeriod);
        setProgramCourses(data);
        setFilteredProgramCourses(data);
        setCourseSearch('');
      } catch (error) {
        setError('Failed to refresh data. Please try again.');
      } finally {
        setLoading(false);
      }
    }
  };

  return (
    <>
      <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold', color: '#333', mb: 3 }}>
        Program Courses Management
      </Typography>
      <TopContainer
        programSearch={programSearch}
        setProgramSearch={setProgramSearch}
        selectedProgram={selectedProgram}
        handleProgramChange={handleProgramChange}
        filteredPrograms={filteredPrograms}
        selectedPeriod={selectedPeriod}
        handlePeriodChange={handlePeriodChange}
        periods={periods}
      />
      {error && (
        <Box sx={{ mt: 2, p: 1.5, bgcolor: '#ffebee', border: '1px solid #f44336' }}>
          <Typography color="error">{error}</Typography>
        </Box>
      )}
      
      {success && (
        <Box sx={{ mt: 2, p: 1.5, bgcolor: '#e8f5e9', border: '1px solid #4caf50' }}>
          <Typography color="success.main">{success}</Typography>
        </Box>
      )}

      {selectedProgram && selectedPeriod && (
        <Box sx={{ mt: 3 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
            <Typography variant="h6" sx={{ color: 'black' }}>
              Courses for Selected Program and Period
            </Typography>
            <Button
              variant="contained"
              color="primary"
              startIcon={<Add />}
              onClick={() => setIsAddDialogOpen(true)}
            >
              Add Course
            </Button>
          </Box>
          <TextField
            fullWidth
            placeholder="Search Courses by Name or Code"
            variant="outlined"
            value={courseSearch}
            onChange={(e) => setCourseSearch(e.target.value)}
            InputProps={{
              startAdornment: <Search color="action" sx={{ mr: 1 }} />,
            }}
            sx={{ mb: 2 }}
          />
          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
              <CircularProgress />
            </Box>
          ) : filteredProgramCourses.length > 0 ? (
            <TableContainer component={Paper} sx={{ border: '1px solid #e0e0e0' }}>
              <Table>
                <TableHead sx={{ bgcolor: '#f5f5f5' }}>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 'bold' }}>Course Code</TableCell>
                    <TableCell sx={{ fontWeight: 'bold' }}>Course Name</TableCell>
                    <TableCell sx={{ fontWeight: 'bold' }}>Credits</TableCell>
                    <TableCell align="center" sx={{ fontWeight: 'bold' }}>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {filteredProgramCourses.map((course) => (
                    <TableRow key={course.programCourseId || `${course.courseCode}-${course.courseName}`}>
                      <TableCell>{course.courseCode}</TableCell>
                      <TableCell>{course.courseName}</TableCell>
                      <TableCell>{course.courseCredit}</TableCell>
                      <TableCell align="center">
                        <IconButton 
                          color="error"
                          onClick={() => confirmDeleteCourse(course.programCourseId)}
                        >
                          <Delete />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          ) : (
            <Box sx={{ p: 3, textAlign: 'center', bgcolor: '#f9f9f9', border: '1px solid #e0e0e0' }}>
              <Typography variant="body1" color="textSecondary">
                No courses found for the selected program and period.
              </Typography>
            </Box>
          )}
        </Box>
      )}

      <Dialog 
        open={isAddDialogOpen} 
        onClose={() => setIsAddDialogOpen(false)}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>Add Course to Program</DialogTitle>
        <DialogContent>
          <FormControl fullWidth>
            <InputLabel>Select Course</InputLabel>
            <Select
              value={selectedCourse}
              onChange={(e) => setSelectedCourse(e.target.value)}
              label="Select Course"
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              {courses.map((course) => (
                <MenuItem key={course.courseId} value={course.courseId}>
                  {course.courseCode} - {course.courseName}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setIsAddDialogOpen(false)} color="inherit">
            Cancel
          </Button>
          <Button 
            onClick={handleAddCourse} 
            color="primary" 
            variant="contained"
            disabled={!selectedCourse}
          >
            Add
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={isDeleteDialogOpen}
        onClose={() => setIsDeleteDialogOpen(false)}
        fullWidth
        maxWidth="xs"
      >
        <DialogTitle>Confirm Deletion</DialogTitle>
        <DialogContent>
          <Typography>
            Are you sure you want to delete this course from the program?
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setIsDeleteDialogOpen(false)} color="inherit">
            Cancel
          </Button>
          <Button 
            onClick={handleDeleteCourse} 
            color="error" 
            variant="contained"
          >
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default ProgramCourse;
