import React from 'react';
import { 
  Grid, 
  TextField, 
  FormControl, 
  InputLabel, 
  Select, 
  MenuItem, 
  Paper, 
  Typography, 
  Box,
  InputAdornment,
  Divider
} from '@mui/material';
import { Search, School, CalendarMonth } from '@mui/icons-material';

const TopContainer = ({
  programSearch,
  setProgramSearch,
  selectedProgram,
  handleProgramChange,
  filteredPrograms,
  selectedPeriod,
  handlePeriodChange,
  periods,
}) => {
  // Filter programs based on the search input
  const displayedPrograms = filteredPrograms.filter((program) =>
    program.programName.toLowerCase().includes(programSearch.toLowerCase()) ||
    program.programCode.toLowerCase().includes(programSearch.toLowerCase())
  );

  return (
    <Paper 
      sx={{ 
        p: 3, 
        mb: 3, 
        border: '1px solid #e0e0e0',
        borderRadius: 2,
        background: 'linear-gradient(to right, #f9f9f9, #ffffff)'
      }}
    >
      <Typography 
        variant="h6" 
        component="h2" 
        gutterBottom 
        sx={{ 
          fontWeight: 600, 
          color: '#1976d2',
          mb: 2,
          display: 'flex',
          alignItems: 'center'
        }}
      >
        <School sx={{ mr: 1 }} /> Program Selection
      </Typography>
      
      <Divider sx={{ mb: 3 }} />
      
      <Grid container spacing={3} alignItems="center">
        <Grid item xs={12} md={4}>
          <FormControl fullWidth>
            <InputLabel 
              id="program-select-label"
              sx={{ 
                fontSize: '1rem',
                '&.MuiInputLabel-shrink': {
                  fontSize: '0.875rem',
                  transform: 'translate(14px, -9px) scale(0.75)',
                } 
              }}
            >
            </InputLabel>
            <Select
              labelId="program-select-label"
              value={selectedProgram}
              onChange={handleProgramChange}
              label="Select Program"
              displayEmpty
              sx={{
                borderRadius: 1.5,
                minHeight: '56px', // Ensure consistent height
                '& .MuiSelect-select': {
                  display: 'flex',
                  alignItems: 'center',
                  padding: '14px',
                  paddingLeft: selectedProgram ? '14px' : '14px',
                }
              }}
              startAdornment={selectedProgram && <School sx={{ ml: 1, mr: 1, color: '#1976d2' }} />}
            >
              <MenuItem value="">
                <Typography sx={{ color: 'text.secondary', fontStyle: 'italic' }}>
                  Please select a program
                </Typography>
              </MenuItem>
              {displayedPrograms.map((program) => (
                <MenuItem 
                  key={program.programId} 
                  value={program.programId}
                  sx={{ 
                    '&.Mui-selected': { 
                      backgroundColor: 'rgba(25, 118, 210, 0.08)'
                    }
                  }}
                >
                  <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
                    <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'left' }}>
                      {program.programName}
                    </Typography>
                  </Box>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        
        <Grid item xs={12} md={4}>
          <FormControl fullWidth>
            <InputLabel 
              id="period-select-label"
              sx={{ 
                fontSize: '1rem',
                '&.MuiInputLabel-shrink': {
                  fontSize: '0.875rem',
                  transform: 'translate(14px, -9px) scale(0.75)',
                } 
              }}
            >
            </InputLabel>
            <Select
              labelId="period-select-label"
              value={selectedPeriod}
              onChange={handlePeriodChange}
              label="Select Period"
              displayEmpty
              disabled={!selectedProgram}
              sx={{
                borderRadius: 1.5,
                minHeight: '56px', // Ensure consistent height
                '& .MuiSelect-select': {
                  display: 'flex',
                  alignItems: 'center',
                  padding: '14px',
                  paddingLeft: selectedPeriod ? '14px' : '14px',
                }
              }}
              startAdornment={selectedPeriod && <CalendarMonth sx={{ ml: 1, mr: 1, color: '#1976d2' }} />}
            >
              <MenuItem value="">
                <Typography sx={{ color: 'text.secondary', fontStyle: 'italic' }}>
                  Please select a period
                </Typography>
              </MenuItem>
              {periods.map((period) => (
                <MenuItem 
                  key={period.periodId} 
                  value={period.periodId}
                  sx={{ 
                    '&.Mui-selected': { 
                      backgroundColor: 'rgba(25, 118, 210, 0.08)'
                    }
                  }}
                >
                  {period.periodName}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
      </Grid>
      
      {selectedProgram && (
        <Box sx={{ mt: 2, display: 'flex', justifyContent: 'flex-end' }}>
          <Typography variant="caption" color="text.secondary">
            {filteredPrograms.find(p => p.programId === selectedProgram)?.programName || ''}
            {selectedPeriod ? ` • ${periods.find(p => p.periodId === selectedPeriod)?.periodName || ''}` : ''}
          </Typography>
        </Box>
      )}
    </Paper>
  );
};

export default TopContainer;